@import "../../../scss/extend.scss";

.contacts {
   padding: 79px 0 169px;
   // .contacts__container

   &__container {}

   // .contacts__title

   &__title {
      margin-bottom: 84px;
   }

   // .contacts__items

   &__items {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;

      @media (max-width:$tablet) {
         flex-wrap: wrap;
         justify-content: center;
         gap: 50px;
      }
   }
}

.title {}

.items-contacts {

   // .items-contacts__picture

   &__picture {}

   // .items-contacts__img

   &__img {
      max-width: 100%;
   }

   // .items-contacts__body

   &__body {
      flex: 0 1 50%;
      min-width: 550px;

      @media (max-width:$tablet) {
         flex: 0 1 100%;
         min-width: 100%;
      }
   }

   // .items-contacts__list

   &__list {}

   // .items-contacts__item

   &__item {
      display: flex;
      align-items: center;
      gap: 45px;

      &:not(:last-child) {
         margin-bottom: 30px;
      }

      @media (max-width:$mobileSmall) {
         gap: 10px;
      }
   }

   // .items-contacts__button

   &__button {
      transition: all 0.3s ease;

      &:hover {
         transform: scale(1.1);
      }
   }

   // .items-contacts__block

   &__block {}

   // .items-contacts__title

   &__title {
      color: $greenColor;
      @extend %text;
      font-weight: 700;
      text-align: start;
   }

   // .items-contacts__text

   &__text {}
}