.icon-menu {
   display: none;

   @media (max-width: $tablet) {
      display: block;
      position: relative;
      width: 30px;
      height: 18px;
      cursor: pointer;
      z-index: 5;


      span,
      &::before,
      &::after {
         content: "";
         transition: all 0.3s ease 0s;
         right: 0;
         position: absolute;
         width: 100%;
         height: 2px;
         background-color: $greenColor;
      }

      &::before {
         top: 0;
      }

      &::after {
         bottom: 0;
      }

      span {
         top: calc(50% - 1px);
      }

      &.menu-open {
         span {
            width: 0;
         }

         &::before {
            top: calc(50% - 1px);
            transform: rotate(-45deg);
         }

         &::after {
            bottom: calc(50% - 1px);
            transform: rotate(45deg);
         }
      }
   }
}

.header {
   padding: 22px 0px;
   font-family: 'SF Pro Display';
   // .header__container

   &__container {
      display: flex;
      justify-content: space-between;
      gap: 30px;
      align-items: center;
   }

   // .header__logo

   &__logo {
      z-index: 3;
   }

   // .header__link

   &__link {}

   // .header__img

   &__img {}

   // .header__menu

   &__menu {}

   // .header__block
   &__block {
      display: flex;
      align-items: center;
      gap: 20px;
   }

   // .header__button

   &__button {
      display: flex;
      padding: 8.5px 24.5px 10.5px;
      gap: 5px;
      color: $greenColor;
      font-weight: normal;
      font-size: 16px;
      line-height: 100%;
      z-index: 3;
   }

   // .header__icon

   &__icon {}

   // .header__text

   &__text {}
}

.menu {
   display: flex;
   flex: 0 1 788px;
   // .menu__icon

   &__icon {}

   // .menu__body

   &__body {
      flex: 0 1 100%;

      @media (max-width:$tablet) {
         position: fixed;
         right: -100%;
         width: 100%;
         top: 0;
         height: 100%;
         background: $bgColor;
         transition: all 0.3s ease;
         overflow: auto;
         z-index: 1;

         &::before {
            content: '';
            position: fixed;
            top: 0;
            right: -100%;
            height: 100px;
            width: 100%;
            background: $bgColor;
            z-index: 2;
            transition: all 0.3s ease;
         }

         &.menu-open {
            right: 0%;

            &::before {
               right: 0%;
            }
         }
      }
   }

   // .menu__list

   &__list {
      display: flex;
      justify-content: space-between;
      flex: 0 1 100%;
      gap: 20px;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;

      @media (max-width:$tablet) {
         padding: 150px 0px 30px;
         font-size: 25px;
         line-height: 35px;
         flex-direction: column;
         align-items: center;
      }
   }

   // .menu__item

   &__item {
      position: relative;
      transition: all 0.3s ease;
      text-align: center;
      cursor: pointer;

      &::before {
         content: '';
         width: 0%;
         height: 2px;
         background: $greenColor;
         position: absolute;
         bottom: -5px;
         left: 0;
         transition: all 0.3s ease;
      }

      &:hover {
         color: $greenColor;

         &.menu__item:before {
            width: 100%;
         }
      }
   }
}