.andromeda {
   padding: 118px 0px 227px;
   background: url('../../../img/andromeda/main-background.png') center/cover no-repeat;
   // .andromeda__container

   &__container {
      display: flex;
      gap: 20px;
      justify-content: space-around;
      align-items: center;

      @media (max-width:$mobile) {
         flex-wrap: wrap-reverse;
         justify-content: center;
      }
   }

   // .andromeda__picture

   &__picture {
      flex: 0 1 50%;

      @media (max-width:$mobile) {
         flex: 0 1 100%;
      }
   }

   // .andromeda__img

   &__img {
      max-width: 100%;
   }

   // .andromeda__body

   &__body {
      display: flex;
      flex-direction: column;

   }

   // .andromeda__title

   &__title {
      text-align: center;
      margin-bottom: 47px;
      // .andromeda__title_color

      &_color {
         color: $mainColor;
      }
   }

   // .andromeda__button

   &__button {
      align-self: center;
      padding: 18px 67.5px 20px;
   }
}

.btn {}