.login {
   padding: 100px 0px 30px;
   // .login__container

   &__container {
      display: flex;
      justify-content: center;
      align-items: center;
      flex: 1 1 auto;
   }

   // .login__body

   &__body {
      flex: 0 1 792px;
      padding: 70px 5px 56px;
      background: #101014;
      border: 1px solid #38383A;
      border-radius: 38px;
      display: grid;
      justify-content: center;
      text-align: center;
      grid-template-columns: minmax(300px, 412px);
   }

   // .login__picture

   &__picture {
      margin-bottom: 59px;

   }

   // .login__icon

   &__icon {}

   // .login__title

   &__title {
      font-weight: 700;
      font-size: 31px;
      line-height: 138.02%;
      color: $greenColor;
      margin-bottom: 48px;
   }

   // .login__form

   &__form {
      display: flex;
      flex-direction: column;
      margin-bottom: 24px;
   }

   // .login__box

   &__box {
      position: relative;

      &:not(:first-child) {
         margin-top: 15px;
      }
   }

   // .login__icon
   &__icon {
      position: absolute;
      left: 27px;

      &_v1 {
         top: 19px;

      }

      &_v2 {
         top: 15px;
      }
   }

   // .login__img

   &__img {}

   // .login__input

   &__input {
      background: $bgColor;
      border: 1px solid #38383A;
      border-radius: 5px;
      padding: 11.5px 50px 13.5px;
      color: $mainColor;
      outline: none;
      width: 100%;

      &::placeholder {
         font-family: 'SF Pro Display';
         font-weight: 400;
         font-size: 16px;
         line-height: 100%;
         color: $greenColor;
      }
   }

   // .login__button

   &__button {
      align-self: center;
      padding: 6px 37px 8px;
      font-size: 17.1327px;
      line-height: 100%;
      margin-top: 64px;
   }

   // .login__link

   &__link {
      font-family: 'SF Pro Display';
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: $mainColor;
      opacity: 0.5;
      transition: all 0.3s ease;

      &:hover {
         opacity: 1;
      }
   }
}