.footer {
   padding: 40px 0;
   // .footer__container

   &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;

      @media (max-width:$mobile) {
         flex-wrap: wrap;
         justify-content: space-around;
         row-gap: 40px;
      }
   }

   // .footer__logo

   &__logo {}

   // .footer__link

   &__link {
      cursor: pointer;
   }

   // .footer__img

   &__img {}

   // .footer__menu

   &__menu {}

   // .footer__social

   &__social {
      display: flex;
      gap: 31px;
   }

   // .footer__button

   &__button {
      transition: all 0.3s ease;

      &:hover {
         transform: scale(1.1);
      }
   }

   // .footer__icon

   &__icon {}
}

.menu-footer {
   flex: 0 1 30%;
   // .menu-footer__body

   &__body {}

   // .menu-footer__list

   &__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      row-gap: 10px;
      column-gap: 40px;
      justify-items: center;
      white-space: nowrap;
   }

   // .menu-footer__item

   &__item {
      position: relative;
      transition: all 0.3s ease;
      text-align: center;
      cursor: pointer;

      &::before {
         content: '';
         width: 0%;
         height: 2px;
         background: $greenColor;
         position: absolute;
         bottom: -5px;
         left: 0;
         transition: all 0.3s ease;
      }

      &:hover {
         color: $greenColor;

         &.menu-footer__item:before {
            width: 100%;
         }
      }
   }

   // .menu-footer__link

   &__link {}
}