@import "../../../scss/extend.scss";

.academy {
   padding: 89px 0 133px;
   // .academy__container

   &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
   }

   // .academy__title

   &__title {
      margin-bottom: 11px;
   }

   // .academy__text

   &__text {
      @extend %text;
   }

   // .academy__items

   &__items {
      display: flex;

      @media (max-width:$mobile) {
         flex-wrap: wrap;
      }
   }
}

.title {}

.btn {}