@import "../../../scss/extend.scss";

.structure {
   padding: 136px 0 285px;
   background: url('../../../img/structure/bg.png') center/cover no-repeat;
   // .structure__container

   &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
   }

   // .structure__title

   &__title {
      margin-bottom: 20px;
   }

   // .structure__text

   &__text {
      @extend %text;
      margin-bottom: 78px;
      // .structure__text_color

      &_color {
         color: $greenColor;
      }
   }

   // .structure__accordion

   &__accordion {
      display: grid;
      grid-template-columns: 1fr 1fr;
      row-gap: 18px;
      column-gap: 20px;

      @media (max-width:$mobile) {
         grid-template-columns: 1fr;
      }
   }
}

.title {}