.tariffs {
   background: url('../../../img/tariffs/tariffs-background.png') center/cover no-repeat;
   // .tariffs__container

   &__container {}

   // .tariffs__title

   &__title {
      margin-bottom: 73px;
   }

   // .tariffs__items

   &__items {
      display: flex;
      align-items: flex-end;
      gap: 70px;
      justify-content: center;

      @media (max-width:$pc) {
         gap: 20px;
      }

      @media (max-width:$tablet) {
         flex-wrap: wrap;
         row-gap: 120px;
      }
   }

   // .tariffs__item

   &__item {}
}

.title {}

.item-tariffs {
   flex: 0 1 363px;
   height: 100%;
   // .item-tariffs_v1

   &_v1 {
      position: relative;

      &::before {
         content: '';
         background: url('../../../img/tariffs/start.png') no-repeat;
         width: 130px;
         height: 130px;
         position: absolute;
         top: -70px;
         left: percentage(110/363);
      }
   }

   // .item-tariffs_v2

   &_v2 {
      position: relative;

      &::before {
         content: '';
         background: url('../../../img/tariffs/vip.png') no-repeat;
         width: 130px;
         height: 130px;
         position: absolute;
         top: -70px;
         left: percentage(110/363);
      }
   }

   // .item-tariffs_v3

   &_v3 {
      position: relative;

      &::before {
         content: '';
         background: url('../../../img/tariffs/pro.png') no-repeat;
         width: 130px;
         height: 130px;
         position: absolute;
         top: -70px;
         left: percentage(110/363);
      }
   }

   // .item-tariffs__block

   &__block {
      display: flex;
      flex-direction: column;
      border: 4px solid $greenColor;
      border-radius: 50px;
      padding: 43px 24px 73px 17px;
      margin-bottom: 16px;
   }

   // .item-tariffs__title

   &__title {
      font-weight: 700;
      font-size: 48px;
      line-height: 138.02%;
      color: $greenColor;
      text-align: center;
      margin-bottom: 21px;
   }

   // .item-tariffs__list

   &__list {
      font-weight: 700;
      margin-bottom: 60px;
   }

   // .item-tariffs__item

   &__item {
      position: relative;
      padding-left: 20px;

      &:not(:last-child) {
         margin-bottom: 16px;
      }

      &::before {
         content: '';
         position: absolute;
         left: 0;
         top: 8px;
         width: 9px;
         height: 9px;
         background: $greenColor;
         border-radius: 100%;
      }
   }

   // .item-tariffs__price

   &__price {
      font-weight: 700;
      font-size: 48px;
      line-height: 138.02%;
      text-align: center;
      color: $greenColor;
      margin-bottom: 4px;
   }

   // .item-tariffs__button

   &__button {
      padding: 6.5px 56.5px 8.5px;
      align-self: center;
   }

   // .item-tariffs__place

   &__place {
      text-align: center;
      font-family: 'SF Pro Display';
      font-weight: 500;
      font-size: 15px;
      line-height: 150.02%;
      color: $greenColor;
      padding: 4px 39px 5px;
      background: conic-gradient(from 219.69deg at 98.19% -26.54%, rgba(121, 121, 121, 0) 0deg, #0F0F13 0deg, #101014 278.86deg, rgba(121, 121, 121, 0) 360deg), #101014;
      mix-blend-mode: normal;
      opacity: 0.72;
      border-radius: 10.8387px;
      max-width: 112px;
      margin: 0 auto;
   }

   // .item-tariffs__text

   &__text {
      text-align: center;
      margin-top: 2px;
      font-family: 'SF Pro Display';
      font-weight: 700;
      font-size: 9px;
      line-height: 150.02%;
   }
}

.btn {}