@import "../../../scss/extend.scss";

.ecosystem {
   padding: 137px 0px 211px;
   // .ecosystem__container

   &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
   }

   // ecosystem__title

   &__title {
      margin-bottom: 11px;
   }

   // .ecosystem__text

   &__text {
      margin-bottom: 130px;
      @extend %text;
   }

   // .ecosystem__items

   &__items {
      display: flex;
      flex-wrap: wrap;
      gap: 47px;
      justify-content: center;
      margin-bottom: 69px;
   }

   // .ecosystem__item

   &__item {}

   // .ecosystem__button

   &__button {
      padding: 18px 94.5px 20px;
   }
}

.item-ecosystem {
   flex: 0 1 400px;
   display: flex;
   flex-direction: column;
   align-items: center;

   // .item-ecosystem__picture

   &__picture {
      margin-bottom: 23px;
   }

   // .item-ecosystem__img

   &__img {}

   // .item-ecosystem__text

   &__text {
      text-align: center;
      font-family: 'SF Pro Display';
      font-weight: 500;
      font-size: 24px;
      line-height: 129.02%;
      // .item-ecosystem__text_color

      &_color {
         color: $greenColor;
      }
   }
}

.title {}

.btn {}