.advantages {
   padding: 41px 0px 158px;
   // .advantages__container

   &__container {}

   // .advantages__title

   &__title {
      margin-bottom: 40px;
   }

   // .advantages__cards

   &__cards {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 26px;
   }

   // .advantages__card

   &__card {}
}

.title {}

.card-advantages {
   flex: 0 1 387px;
   display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   background: conic-gradient(from 219.69deg at 98.19% -26.54%, rgba(121, 121, 121, 0) 0deg, #0F0F13 0.04deg, #101014 108.75deg, rgba(221, 221, 221, 0) 360deg), #101014;
   mix-blend-mode: normal;
   padding: 42px 24px 57px;
   // .card-advantages__picture

   &__picture {}

   // .card-advantages__img

   &__img {}

   // .card-advantages__title

   &__title {
      font-weight: 700;
      font-size: 24px;
      line-height: 150.02%;
      color: $greenColor;
   }

   // .card-advantages__text

   &__text {
      font-family: 'SF Pro Display';
      font-style: normal;
      font-weight: 500;
      line-height: 150.02%;
   }
}