///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
/// Стили для повторяющихся БЭМ блоков
///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
.btn {
   border: 1px solid #38383A;
   border-radius: 5px;
   background: $bgColor;
   transition: all 0.3s ease;
   color: $greenColor;
   text-align: center;
   white-space: nowrap;
   cursor: pointer;
   display: inline-block;

   &:hover {
      border: 1px solid #fff;
   }
}

.title {
   color: $greenColor;
   font-weight: 700;
   font-size: 56px;
   line-height: 138.02%;
   text-align: center;

   @media (max-width:$mobile) {
      font-size: calc(var(--index) * 4);
      line-height: calc(var(--index) * 4);
   }

   &.andromeda__title {
      font-weight: 700;
      font-size: calc(var(--index) * 3);
      line-height: calc(var(--index) * 4);
   }
}

.twin {
   margin-top: 30px;
   align-items: center;
   // .twin__picture

   &__picture {}

   // .twin__img

   &__img {
      max-width: 100%;
   }

   // .twin__body

   &__body {
      flex: 0 1 50%;

      @media (max-width:$tablet) {
         flex: 0 1 80%;
      }

      @media (max-width:$mobile) {
         flex: 0 1 100%;
      }
   }

   // .twin__list

   &__list {
      margin-bottom: 38px;
   }

   // .twin__item

   &__item {
      &_color {
         color: $greenColor;
      }

      &:not(:last-child) {
         margin-bottom: 22px;
      }
   }

   // .twin__button

   &__button {
      padding: 18px 43px 20px;
   }
}

.accordion {

   // .accordion__item

   &__item {
      background: conic-gradient(from 219.69deg at 98.19% -26.54%, rgba(121, 121, 121, 0) 0deg, #0F0F13 0deg, #101014 278.86deg, rgba(121, 121, 121, 0) 360deg), #101014;
      mix-blend-mode: normal;
      backdrop-filter: blur(111.5px);
      border-radius: 18px;
      padding: 17px 30px;
      opacity: 0.75;
   }

   // .accordion__heading

   &__heading {
      font-family: 'SF Pro Display';
      font-weight: 500;
      font-size: 24px;
      line-height: 150.02%;
   }

   // .accordion__button

   &__button {
      display: flex;
      gap: 20px;
      justify-content: space-between;
      cursor: pointer;
   }

   // .accordion__title

   &__title {
