}

.accordion__button[aria-expanded='true'],
.accordion__button[aria-selected='true'] {
   .accordion__icon {
      transform: rotate(45deg);
   }
}

@keyframes fadein {
   0% {
      opacity: 0;
   }

   100% {
      opacity: 1;
   }
}